.acctWrapper {
  background-color: rgb(1, 95, 153);
  width: 30%;
  height: 445px;
  border-radius: 12px;
  margin: 34px 123px;
}
.acctWrapper form {
  display: flex;
  flex-direction: column;
}
.acct {
  display: flex;
}
.acctWrapper p {
  padding: 22px;
  border-bottom: 1px solid rgb(224, 224, 224);
  color: white;
}

form {
  padding: 26px;
}

form label {
  margin-top: 17px;
  color: white;
}
form input {
  height: 43px;
  border-radius: 12px;
  border: none;
  outline: none;
  padding-left: 12px;
}

#ol {
  margin-top: 18px;
  width: 234px;
}
.success {
  text-align: center;
  color: white;
}

#oio {
  margin: 63px;
  width: 223px;
  padding: 11px;
  border: none;
}
