.navbar{
    background-color: gray;
    display: flex;
    height: 73px;
}
.navbar ul{
    display: flex;
    list-style: none;
    margin: auto;
    align-items: center;
    text-decoration: none;
}
.navbar ul li{
    margin-left: 27px;
    font-size: 21px;
    color: white;
    cursor: pointer;
   
}
.navbar ul li span{
    font-size: 32px;
    cursor: pointer;
}
