.bWrapper {
    background-color: rgb(2, 69, 86);
    width: 30%;
    height: 295px;
    border-radius: 12px;
    margin: 34px 123px;
  }
  .bWrapper form {
    display: flex;
    flex-direction: column;
  }
  .acct {
    display: flex;
  }
  .bWrapper p {
    padding: 22px;
    border-bottom: 1px solid rgb(224, 224, 224);
    color: white;
  }
  
  form {
    padding: 26px;
  }
  
  form label {
    margin-top: 17px;
    color: white;
  }
  form input {
    height: 43px;
    border-radius: 12px;
    border: none;
    outline: none;
    padding-left: 12px;
  }
  
  #ol {
    margin-top: 18px;
    width: 234px;
  }
  .success {
    text-align: center;
    color: white;
  }
  
  #oio {
    margin: 63px;
    width: 223px;
    padding: 11px;
    border: none;
  }
  